import React, {Component} from "react"
import {gsap} from "gsap";
import {TimelineLite, TweenLite, ScrollTrigger, ScrollToPlugin} from "gsap/all";
import Scrollbar from "smooth-scrollbar";

import FooterLink from "./Footer/FooterLink";
import SectionContactUs from"./Footer/SectionContactUs"

import '../scss/footer.scss'

gsap.registerPlugin(TimelineLite, TweenLite, ScrollTrigger, ScrollToPlugin);

class Footer extends Component {
    constructor(props) {
        super(props);

        this.linksSection = null;

        this.menuAnim = null;
        this.menuController = null;
        this.menu = null;
        this.menuIsOpen = false;
        this.hoverMenuAction = null;

        this.socialAnim = null;
        this.socialController = null;
        this.social = null;
        this.socialIsOpen = true;
        this.hoverSocialAction = null;
    }

    componentDidMount() {
        const scroller = document.querySelector('.scroller');
        const bodyScrollBar = Scrollbar.init(scroller, {damping: 0.05, delegateTo: document, alwaysShowTracks: false});
        ScrollTrigger.scrollerProxy(".scroller", {
            scrollTop(value) {
                if (arguments.length) {
                    bodyScrollBar.scrollTop = value;
                }
                return bodyScrollBar.scrollTop;
            }
        });
        bodyScrollBar.addListener(ScrollTrigger.update);
        ScrollTrigger.defaults({scroller: scroller});


        gsap.set(this.menu, {
            zIndex: 3,
            borderRadius: '50% 0 0 50% ',
            width: 200,
            height: 200,
            right: -100,
        })
        // gsap.set([this.socialController,this.social], {
        //     left: -200,
        // })
    }

    overSocial = () =>{
        if (!this.socialIsOpen) gsap.to([this.social, this.socialController], {duration: 0.2, ease: "none",height: '120vh',width: 400,left: -200});
    }
    outSocial  = () =>{
        if (!this.socialIsOpen) gsap.to([this.social, this.socialController], {duration: 0.2, ease: "none",height: 200,width: 200,left: -100});
    }

    openSocial = () => {
        this.menuIsOpen = false;
        this.socialIsOpen = true;

        this.socialAnim = new TimelineLite();
        this.socialAnim.to(this.social,0.4,{ ease:"none",height: '120vh', width: '120vw',} );
        this.socialAnim.to(this.social,0.2,{ ease:"none", borderRadius: '0'} );
        this.socialAnim.to(this.socialController,0.2,{ ease:"none", opacity: 0}, "=-0.6" );
        this.socialAnim.to([this.social, this.socialController],0,{ ease:"none", zIndex: 0},);
        this.socialAnim.to([this.menu, this.menuController], 0, {
            ease: "none",
            opacity: 1,
            borderRadius: '50% 0 0 50%',
            width: 200,
            height: 200,
            right: -200,
            zIndex: 3
        },);
        this.socialAnim.to([this.menu, this.menuController],0.2,{ right: -100}, "=+0.2" );
    }

    overMenu = () =>{
        if (!this.menuIsOpen) gsap.to([this.menu, this.menuController], {duration: 0.2, ease: "none",height: '120vh',width: 400, right: -200});
    }
    outMenu  = () =>{
        if (!this.menuIsOpen) gsap.to([this.menu, this.menuController], {duration: 0.2, ease: "none",height: 200,width: 200,right: -100});
    }

    openMenu = () => {
        this.menuIsOpen = true;
        this.socialIsOpen = false;

        this.menuAnim = new TimelineLite();
        this.menuAnim.to(this.menu,0.4,{ ease:"none",height: '120vh', width: '120vw', } );
        this.menuAnim.to(this.menu,0.2,{ ease:"none", borderRadius: '0'} );
        this.menuAnim.to(this.menuController,0.2,{ ease:"none", opacity: 0}, "=-0.6" );
        this.menuAnim.to([this.menu, this.menuController],0,{ ease:"none", zIndex: 0},);
        this.menuAnim.to([this.social, this.socialController], 0, {
            opacity: 1,
            ease: "none",
            borderRadius: '0 50% 50% 0',
            width: 200,
            height: 200,
            left: -200,
            zIndex: 3
        },);
        this.menuAnim.to([this.social, this.socialController],0.2,{ left: -100}, "=+0.2" );
    }

    render() {
        return (
            <footer>
                <SectionContactUs/>

                <section className="links" ref={section => this.linksSection = section}>
                    <div className="social wrapper" ref={div => this.social = div}>
                        <div className="link-block">
                            <FooterLink to=''>FACEBOOK</FooterLink>
                            <FooterLink to=''>INSTAGRAM</FooterLink>
                            <FooterLink to=''>LINKEDIN</FooterLink>
                            <FooterLink to=''>BEHANCE</FooterLink>
                            <FooterLink to=''>EMAIL</FooterLink>
                        </div>
                    </div>
                    <div className="controller left" ref={div => this.socialController = div}
                         onMouseOver={this.overSocial}
                         onMouseLeave={this.outSocial}
                         onClick={this.openSocial}>
                        <div className="text">SOCIAL</div>
                        <div className="arrow"/>
                    </div>
                    <div className="menu wrapper closed" ref={div => this.menu = div}>
                        <div className="link-block">
                            <FooterLink to=''>Home</FooterLink>
                            <FooterLink to=''>Our Work</FooterLink>
                            <FooterLink to=''>Services</FooterLink>
                            <FooterLink to=''>About Us</FooterLink>
                            <FooterLink to=''>Contact</FooterLink>
                        </div>
                    </div>
                    <div className="controller right closed" ref={div => this.menuController = div}
                         onMouseOver ={this.overMenu}
                         onMouseLeave ={this.outMenu}
                         onClick={this.openMenu}>
                        <div className="arrow"/>
                        <div className="text">MENU</div>
                    </div>
                </section>

            </footer>
        );
    }
}

export default Footer
